<template><div class="markdown-body"><h2 id="chou-ti-dao-hang"><a class="header-anchor" href="#chou-ti-dao-hang" aria-hidden="true">¶</a> 抽屉导航</h2>
<p><code v-pre="">mu-drawer</code> 从侧边弹出的对话框。</p>
<h2 id="shi-li"><a class="header-anchor" href="#shi-li" aria-hidden="true">¶</a> 示例</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-flex justify-content=\&quot;center\&quot; align-items=\&quot;center\&quot;&gt;\n    &lt;mu-switch style=\&quot;margin-left: 16px\&quot; v-model=\&quot;docked\&quot; label=\&quot;docked\&quot;&gt;&lt;/mu-switch&gt;\n    &lt;mu-radio style=\&quot;margin-left: 16px\&quot; v-model=\&quot;position\&quot; value=\&quot;left\&quot; label=\&quot;left\&quot;&gt;&lt;/mu-radio&gt;\n    &lt;mu-radio style=\&quot;margin-left: 16px\&quot; v-model=\&quot;position\&quot; value=\&quot;right\&quot; label=\&quot;right\&quot;&gt;&lt;/mu-radio&gt;\n    &lt;mu-button color=\&quot;primary\&quot; style=\&quot;margin-left: 16px\&quot; @click=\&quot;open = !open\&quot;&gt;\n      toggle\n    &lt;/mu-button&gt;\n  &lt;/mu-flex&gt;\n  &lt;mu-drawer :open.sync=\&quot;open\&quot; :docked=\&quot;docked\&quot; :right=\&quot;position === 'right'\&quot;&gt;\n    &lt;mu-list&gt;\n      &lt;mu-list-item button&gt;\n        &lt;mu-list-item-title&gt;Menu Item 1&lt;/mu-list-item-title&gt;\n      &lt;/mu-list-item&gt;\n      &lt;mu-list-item button&gt;\n        &lt;mu-list-item-title&gt;Menu Item 2&lt;/mu-list-item-title&gt;\n      &lt;/mu-list-item&gt;\n      &lt;mu-list-item button&gt;\n        &lt;mu-list-item-title @click=\&quot;open = false\&quot;&gt;Close&lt;/mu-list-item-title&gt;\n      &lt;/mu-list-item&gt;\n    &lt;/mu-list&gt;\n  &lt;/mu-drawer&gt;\n&lt;mu-container&gt;\n\n&lt;/mu-container&gt;&lt;/mu-container&gt;&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      docked: false,\n      open: false,\n      position: 'left'\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-container>
  <mu-flex justify-content="center" align-items="center">
    <mu-switch style="margin-left: 16px" v-model="docked" label="docked"></mu-switch>
    <mu-radio style="margin-left: 16px" v-model="position" value="left" label="left"></mu-radio>
    <mu-radio style="margin-left: 16px" v-model="position" value="right" label="right"></mu-radio>
    <mu-button color="primary" style="margin-left: 16px" @click="open = !open">
      toggle
    </mu-button>
  </mu-flex>
  <mu-drawer :open.sync="open" :docked="docked" :right="position === 'right'">
    <mu-list>
      <mu-list-item button="">
        <mu-list-item-title>Menu Item 1</mu-list-item-title>
      </mu-list-item>
      <mu-list-item button="">
        <mu-list-item-title>Menu Item 2</mu-list-item-title>
      </mu-list-item>
      <mu-list-item button="">
        <mu-list-item-title @click="open = false">Close</mu-list-item-title>
      </mu-list-item>
    </mu-list>
  </mu-drawer>
<mu-container>

</mu-container></mu-container></div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-flex</span> <span class="hljs-attr">justify-content</span>=<span class="hljs-string">"center"</span> <span class="hljs-attr">align-items</span>=<span class="hljs-string">"center"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-switch</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"margin-left: 16px"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"docked"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"docked"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-switch</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-radio</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"margin-left: 16px"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"position"</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"left"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"left"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-radio</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"margin-left: 16px"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"position"</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"right"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"right"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"margin-left: 16px"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"open = !open"</span>&gt;</span>
      toggle
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-flex</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-drawer</span> <span class="hljs-attr">:open.sync</span>=<span class="hljs-string">"open"</span> <span class="hljs-attr">:docked</span>=<span class="hljs-string">"docked"</span> <span class="hljs-attr">:right</span>=<span class="hljs-string">"position === 'right'"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-list</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item</span> <span class="hljs-attr">button</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span>&gt;</span>Menu Item 1<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item</span> <span class="hljs-attr">button</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span>&gt;</span>Menu Item 2<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item</span> <span class="hljs-attr">button</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"open = false"</span>&gt;</span>Close<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-drawer</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">docked</span>: <span class="hljs-literal">false</span>,
      <span class="hljs-attr">open</span>: <span class="hljs-literal">false</span>,
      <span class="hljs-attr">position</span>: <span class="hljs-string">'left'</span>
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="drawer-props"><a class="header-anchor" href="#drawer-props" aria-hidden="true">¶</a> Drawer Props</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>open <code v-pre="">.sync</code></td>
<td>是否打开</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>right</td>
<td>是否从右侧滑出</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>docked</td>
<td>是否固定 drawer， 设为 <code v-pre="">true</code>， 将不会显示遮盖层，也不会触发 <code v-pre="">close</code> 事件</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>width</td>
<td>drawer 宽度</td>
<td>String/Number</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>z-depth</td>
<td>阴影的深度</td>
<td>Number</td>
<td>0 — 5</td>
<td>2</td>
</tr>
</tbody>
</table>

</div class="markdown-body"></template>

<script>
export default {
  data () {
    return {
      docked: false,
      open: false,
      position: 'left'
    }
  }
}
</script>


